<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router';
import SelectGoalList from './SelectGoalList.vue';
export default {
  name: "TutoPrivateProfile",
setup(){
  const router = useRouter();

  const state = reactive({
      token: sessionStorage.getItem("TOKEN"),
    })
  const deleteAll = async() =>{
    const url = "http://localhost:9090/api/tutoDelete";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token };
      const body = {
        email: 'hyelan@ds.org',
      };
      const response = await axios.post(url, body, {headers}).then((res)=>{
        if(res.data != null){
          router.push({ name: "tutohome" })
        }
      }).catch((error)=>{
        console.log("goal and calendar is not deleted"+error)
      })

  }
return{deleteAll,state}
}
}
